import React from 'react';
import { Container, Grid, Card, CardContent, Typography } from '@mui/material';
import { Line, Bar } from 'react-chartjs-2';

// Example Player Data
const playerData = {
  name: 'John Doe',
  age: 12,
  diagnosis: 'Autism Spectrum Disorder (ASD)',
  sessionsCompleted: 15,
  profileImage: 'https://via.placeholder.com/150',
};

// Example Metrics Data for Graphs
const sessionData = {
  labels: ['Session 1', 'Session 2', 'Session 3', 'Session 4', 'Session 5'],
  datasets: [
    {
      label: 'Positive Interaction Score',
      data: [55, 65, 70, 75, 80],
      borderColor: 'rgba(75, 192, 192, 1)',
      fill: false,
    },
    {
      label: 'Negative Interaction Score',
      data: [45, 35, 30, 25, 20],
      borderColor: 'rgba(255, 99, 132, 1)',
      fill: false,
    },
  ],
};

const emotionData = {
  labels: ['Happy', 'Sad', 'Angry', 'Calm', 'Confused'],
  datasets: [
    {
      label: 'Emotional Distribution',
      data: [30, 10, 5, 40, 15],
      backgroundColor: [
        'rgba(255, 99, 132, 0.2)',
        'rgba(54, 162, 235, 0.2)',
        'rgba(255, 206, 86, 0.2)',
        'rgba(75, 192, 192, 0.2)',
        'rgba(153, 102, 255, 0.2)',
      ],
      borderColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(153, 102, 255, 1)',
      ],
      borderWidth: 1,
    },
  ],
};

const Dashboard = () => {
  return (
    <Container maxWidth="lg" style={{ marginTop: '20px' }}>
      <Grid container spacing={3}>
        {/* Player Profile Section */}
        <Grid item xs={12} sm={4}>
          <Card>
            <CardContent>
              <Typography variant="h5" component="div" gutterBottom>
                Player Profile
              </Typography>
              <img src={playerData.profileImage} alt="Player" style={{ width: '100px', borderRadius: '50%' }} />
              <Typography variant="body1">Name: {playerData.name}</Typography>
              <Typography variant="body1">Age: {playerData.age}</Typography>
              <Typography variant="body1">Diagnosis: {playerData.diagnosis}</Typography>
              <Typography variant="body1">Sessions Completed: {playerData.sessionsCompleted}</Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Session Performance Section */}
        <Grid item xs={12} sm={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" component="div" gutterBottom>
                Session Performance Over Time
              </Typography>
              <Line
                data={sessionData}
                options={{
                  responsive: true,
                  scales: {
                    x: { beginAtZero: true },
                    y: { beginAtZero: true },
                  },
                }}
              />
            </CardContent>
          </Card>
        </Grid>

        {/* Emotion Distribution Section */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" component="div" gutterBottom>
                Emotion Distribution
              </Typography>
              <Bar
                data={emotionData}
                options={{
                  responsive: true,
                  scales: {
                    x: { beginAtZero: true },
                    y: { beginAtZero: true },
                  },
                }}
              />
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Dashboard;
